body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
}

html,

body,
#potato,

.container{
    height: 100%;
    display: flex;
    justify-content:  center;
}


.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies .movie {
    width:  45%;
    background-color: white;
    margin-bottom:  70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    position: relative;


    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
    position: relative;
    top:-50px;
    margin-right: 30px;
    max-width: 120px;
}


.movie h3,
.movie h5 {
    margin: 0;
    font-weight: 300;
}

.movie h3 {
    margin-bottom: 5px;
    font-size: 24px;
}

.movie h5,
.movie .movie_year {
    opacity: 0.3;
}